import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import {
  BookOpenIcon,
  ClockIcon,
  ChartBarIcon,
  FireIcon,
  CalendarDaysIcon,
  ExclamationTriangleIcon,
  CheckCircleIcon,
  PlayCircleIcon,
  ChatBubbleLeftRightIcon,
  BellAlertIcon
} from '@heroicons/react/24/outline';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';

const DashboardPage = () => {
  const { user } = useAuth();
  const [selectedDate, setSelectedDate] = useState(new Date());

  // Sample enrolled courses data
  const enrolledCourses = [
    {
      id: 1,
      title: "Introduction to React.js",
      instructor: "Dr. Sarah Johnson",
      progress: 65,
      totalWeeks: 8,
      completedWeeks: 5,
      nextDeadline: "2024-10-15",
      nextAssignment: "React Hooks Assignment",
      status: "in_progress",
      thumbnail: "https://via.placeholder.com/300x200/3B82F6/FFFFFF?text=React.js",
      timeSpent: 25 // hours
    },
    {
      id: 2,
      title: "Data Structures and Algorithms",
      instructor: "Prof. Michael Chen",
      progress: 30,
      totalWeeks: 12,
      completedWeeks: 4,
      nextDeadline: "2024-10-18",
      nextAssignment: "Binary Trees Implementation",
      status: "in_progress",
      thumbnail: "https://via.placeholder.com/300x200/10B981/FFFFFF?text=DSA",
      timeSpent: 18
    },
    {
      id: 3,
      title: "Digital Marketing Fundamentals",
      instructor: "Emily Rodriguez",
      progress: 100,
      totalWeeks: 6,
      completedWeeks: 6,
      nextDeadline: null,
      nextAssignment: null,
      status: "completed",
      thumbnail: "https://via.placeholder.com/300x200/F59E0B/FFFFFF?text=Marketing",
      timeSpent: 24,
      completedDate: "2024-09-20",
      certificateUrl: "#"
    }
  ];

  // Sample upcoming assignments
  const upcomingAssignments = [
    {
      id: 1,
      title: "React Hooks Assignment",
      course: "Introduction to React.js",
      dueDate: "2024-10-15",
      daysLeft: 3,
      type: "assignment",
      priority: "high"
    },
    {
      id: 2,
      title: "Binary Trees Implementation",
      course: "Data Structures and Algorithms",
      dueDate: "2024-10-18",
      daysLeft: 6,
      type: "project",
      priority: "medium"
    },
    {
      id: 3,
      title: "Algorithm Analysis Quiz",
      course: "Data Structures and Algorithms",
      dueDate: "2024-10-22",
      daysLeft: 10,
      type: "quiz",
      priority: "low"
    }
  ];

  // Sample recent activities
  const recentActivities = [
    {
      id: 1,
      type: "assignment_submitted",
      title: "Submitted JavaScript Fundamentals Assignment",
      course: "Introduction to React.js",
      timestamp: "2 hours ago",
      icon: CheckCircleIcon,
      color: "text-green-600"
    },
    {
      id: 2,
      type: "week_completed",
      title: "Completed Week 5: Advanced React Patterns",
      course: "Introduction to React.js",
      timestamp: "1 day ago",
      icon: PlayCircleIcon,
      color: "text-blue-600"
    },
    {
      id: 3,
      type: "forum_reply",
      title: "Replied to discussion about State Management",
      course: "Introduction to React.js",
      timestamp: "2 days ago",
      icon: ChatBubbleLeftRightIcon,
      color: "text-purple-600"
    },
    {
      id: 4,
      type: "certificate_earned",
      title: "Earned Certificate for Digital Marketing",
      course: "Digital Marketing Fundamentals",
      timestamp: "1 week ago",
      icon: CheckCircleIcon,
      color: "text-yellow-600"
    }
  ];

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric'
    });
  };

  const getPriorityColor = (priority) => {
    switch (priority) {
      case 'high': return 'text-red-600 bg-red-50 border-red-200';
      case 'medium': return 'text-yellow-600 bg-yellow-50 border-yellow-200';
      case 'low': return 'text-green-600 bg-green-50 border-green-200';
      default: return 'text-gray-600 bg-gray-50 border-gray-200';
    }
  };

  const getTypeIcon = (type) => {
    switch (type) {
      case 'assignment': return '';
      case 'project': return '';
      case 'quiz': return '';
      default: return '';
    }
  };

  // Calculate overall stats
  const totalCourses = enrolledCourses.length;
  const completedCourses = enrolledCourses.filter(c => c.status === 'completed').length;
  const inProgressCourses = enrolledCourses.filter(c => c.status === 'in_progress').length;
  const totalTimeSpent = enrolledCourses.reduce((sum, course) => sum + course.timeSpent, 0);
  const averageProgress = enrolledCourses.reduce((sum, course) => sum + course.progress, 0) / totalCourses;

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Welcome Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            Welcome back, {user?.name || 'Student'}! 
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-2">
            Here's your learning progress and upcoming assignments.
          </p>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
            <div className="flex items-center">
              <div className="p-3 bg-blue-100 dark:bg-blue-900 rounded-full">
                <BookOpenIcon className="w-6 h-6 text-blue-600 dark:text-blue-400" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Total Courses</p>
                <p className="text-2xl font-semibold text-gray-900 dark:text-white">{totalCourses}</p>
              </div>
            </div>
          </div>

          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
            <div className="flex items-center">
              <div className="p-3 bg-green-100 dark:bg-green-900 rounded-full">
                <CheckCircleIcon className="w-6 h-6 text-green-600 dark:text-green-400" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Completed</p>
                <p className="text-2xl font-semibold text-gray-900 dark:text-white">{completedCourses}</p>
              </div>
            </div>
          </div>

          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
            <div className="flex items-center">
              <div className="p-3 bg-purple-100 dark:bg-purple-900 rounded-full">
                <ChartBarIcon className="w-6 h-6 text-purple-600 dark:text-purple-400" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Avg Progress</p>
                <p className="text-2xl font-semibold text-gray-900 dark:text-white">{Math.round(averageProgress)}%</p>
              </div>
            </div>
          </div>

          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
            <div className="flex items-center">
              <div className="p-3 bg-orange-100 dark:bg-orange-900 rounded-full">
                <ClockIcon className="w-6 h-6 text-orange-600 dark:text-orange-400" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Hours Studied</p>
                <p className="text-2xl font-semibold text-gray-900 dark:text-white">{totalTimeSpent}</p>
              </div>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Column */}
          <div className="lg:col-span-2 space-y-8">
            {/* Current Courses */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md">
              <div className="p-6 border-b border-gray-200 dark:border-gray-700">
                <h2 className="text-xl font-semibold text-gray-900 dark:text-white">My Courses</h2>
              </div>
              <div className="p-6">
                <div className="space-y-6">
                  {enrolledCourses.map(course => (
                    <div key={course.id} className="flex items-center space-x-4 p-4 bg-gray-50 dark:bg-gray-700 rounded-lg">
                      <img
                        src={course.thumbnail}
                        alt={course.title}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                      <div className="flex-1">
                        <h3 className="font-semibold text-gray-900 dark:text-white">{course.title}</h3>
                        <p className="text-sm text-gray-600 dark:text-gray-400">by {course.instructor}</p>
                        
                        {/* Progress Bar */}
                        <div className="mt-2">
                          <div className="flex justify-between text-sm text-gray-600 dark:text-gray-400 mb-1">
                            <span>Progress: {course.completedWeeks}/{course.totalWeeks} weeks</span>
                            <span>{course.progress}%</span>
                          </div>
                          <div className="progress-bar">
                            <div 
                              className="progress-fill" 
                              style={{ width: `${course.progress}%` }}
                            ></div>
                          </div>
                        </div>
                        
                        {/* Next Assignment */}
                        {course.nextAssignment && (
                          <div className="mt-2 flex items-center text-sm text-orange-600 dark:text-orange-400">
                            <ExclamationTriangleIcon className="w-4 h-4 mr-1" />
                            <span>Next: {course.nextAssignment} - Due {formatDate(course.nextDeadline)}</span>
                          </div>
                        )}
                        
                        {/* Completed Badge */}
                        {course.status === 'completed' && (
                          <div className="mt-2 flex items-center text-sm text-green-600 dark:text-green-400">
                            <CheckCircleIcon className="w-4 h-4 mr-1" />
                            <span>Completed on {formatDate(course.completedDate)}</span>
                            <Link to={course.certificateUrl} className="ml-2 text-blue-600 hover:underline">
                              View Certificate
                            </Link>
                          </div>
                        )}
                      </div>
                      
                      <div className="flex flex-col space-y-2">
                        <Link
                          to={`/courses/${course.id}`}
                          className="btn-primary text-sm px-3 py-1"
                        >
                          Continue
                        </Link>
                        <Link
                          to={`/forum/${course.id}`}
                          className="btn-outline text-sm px-3 py-1 flex items-center"
                        >
                          <ChatBubbleLeftRightIcon className="w-4 h-4 mr-1" />
                          Forum
                        </Link>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* Recent Activity */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md">
              <div className="p-6 border-b border-gray-200 dark:border-gray-700">
                <h2 className="text-xl font-semibold text-gray-900 dark:text-white">Recent Activity</h2>
              </div>
              <div className="p-6">
                <div className="space-y-4">
                  {recentActivities.map(activity => (
                    <div key={activity.id} className="flex items-start space-x-3">
                      <div className={`p-2 rounded-full ${activity.color.replace('text-', 'bg-').replace('600', '100')} dark:${activity.color.replace('text-', 'bg-').replace('600', '900')}`}>
                        <activity.icon className={`w-4 h-4 ${activity.color}`} />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900 dark:text-white">{activity.title}</p>
                        <p className="text-xs text-gray-600 dark:text-gray-400">{activity.course}  {activity.timestamp}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Right Column */}
          <div className="space-y-8">
            {/* Upcoming Assignments */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md">
              <div className="p-6 border-b border-gray-200 dark:border-gray-700">
                <div className="flex items-center justify-between">
                  <h2 className="text-xl font-semibold text-gray-900 dark:text-white">Upcoming Deadlines</h2>
                  <BellAlertIcon className="w-5 h-5 text-orange-500" />
                </div>
              </div>
              <div className="p-6">
                <div className="space-y-4">
                  {upcomingAssignments.map(assignment => (
                    <div key={assignment.id} className={`p-3 rounded-lg border ${getPriorityColor(assignment.priority)}`}>
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <div className="flex items-center space-x-2">
                            <span className="text-lg">{getTypeIcon(assignment.type)}</span>
                            <h3 className="font-medium text-gray-900 dark:text-white text-sm">{assignment.title}</h3>
                          </div>
                          <p className="text-xs text-gray-600 dark:text-gray-400 mt-1">{assignment.course}</p>
                          <div className="flex items-center justify-between mt-2">
                            <span className="text-xs font-medium text-gray-900 dark:text-white">
                              Due {formatDate(assignment.dueDate)}
                            </span>
                            <span className={`text-xs font-bold ${
                              assignment.daysLeft <= 3 ? 'text-red-600' :
                              assignment.daysLeft <= 7 ? 'text-orange-600' : 'text-green-600'
                            }`}>
                              {assignment.daysLeft} days left
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
                <Link to="/assignments" className="block mt-4 text-center btn-outline text-sm py-2">
                  View All Assignments
                </Link>
              </div>
            </div>

            {/* Calendar */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md">
              <div className="p-6 border-b border-gray-200 dark:border-gray-700">
                <h2 className="text-xl font-semibold text-gray-900 dark:text-white">Calendar</h2>
              </div>
              <div className="p-6">
                <Calendar
                  onChange={setSelectedDate}
                  value={selectedDate}
                  className="react-calendar"
                  tileClassName={({ date }) => {
                    const dateStr = date.toISOString().split('T')[0];
                    const hasDeadline = upcomingAssignments.some(a => a.dueDate === dateStr);
                    return hasDeadline ? 'react-calendar__tile--hasActive' : '';
                  }}
                />
              </div>
            </div>

            {/* Learning Streak */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md">
              <div className="p-6 text-center">
                <div className="p-3 bg-orange-100 dark:bg-orange-900 rounded-full inline-flex mb-4">
                  <FireIcon className="w-8 h-8 text-orange-600 dark:text-orange-400" />
                </div>
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">Learning Streak</h3>
                <p className="text-3xl font-bold text-orange-600 dark:text-orange-400 mb-2">{user?.learningStreak || 7} days</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Keep it up! You're doing great!</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;
