import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { 
  MagnifyingGlassIcon, 
  StarIcon, 
  ClockIcon,
  UserGroupIcon,
  AcademicCapIcon
} from '@heroicons/react/24/outline';
import { StarIcon as StarSolid } from '@heroicons/react/24/solid';

const CoursesPage = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedDifficulty, setSelectedDifficulty] = useState('all');

  // Sample courses data
  const courses = [
    {
      id: 1,
      title: "Introduction to React.js",
      instructor: "Dr. Sarah Johnson",
      institution: "Tech University",
      description: "Learn the fundamentals of React.js and build modern web applications with components, hooks, and state management.",
      category: "Computer Science",
      difficulty: "Beginner",
      duration: { weeks: 8, hoursPerWeek: 5 },
      rating: { average: 4.8, count: 1250 },
      enrollmentCount: 15420,
      thumbnail: "https://via.placeholder.com/300x200/3B82F6/FFFFFF?text=React.js",
      tags: ["JavaScript", "Web Development", "Frontend"]
    },
    {
      id: 2,
      title: "Data Structures and Algorithms",
      instructor: "Prof. Michael Chen",
      institution: "Computer Science Institute",
      description: "Master fundamental data structures and algorithms essential for programming interviews and software development.",
      category: "Computer Science",
      difficulty: "Intermediate",
      duration: { weeks: 12, hoursPerWeek: 6 },
      rating: { average: 4.9, count: 2100 },
      enrollmentCount: 8950,
      thumbnail: "https://via.placeholder.com/300x200/10B981/FFFFFF?text=DSA",
      tags: ["Algorithms", "Programming", "Problem Solving"]
    },
    {
      id: 3,
      title: "Digital Marketing Fundamentals",
      instructor: "Emily Rodriguez",
      institution: "Business School",
      description: "Learn digital marketing strategies, SEO, social media marketing, and analytics to grow your business online.",
      category: "Management",
      difficulty: "Beginner",
      duration: { weeks: 6, hoursPerWeek: 4 },
      rating: { average: 4.6, count: 890 },
      enrollmentCount: 12300,
      thumbnail: "https://via.placeholder.com/300x200/F59E0B/FFFFFF?text=Marketing",
      tags: ["Marketing", "SEO", "Social Media"]
    },
    {
      id: 4,
      title: "Machine Learning with Python",
      instructor: "Dr. Rajesh Patel",
      institution: "AI Research Center",
      description: "Dive into machine learning algorithms, neural networks, and AI applications using Python and popular libraries.",
      category: "Computer Science",
      difficulty: "Advanced",
      duration: { weeks: 16, hoursPerWeek: 8 },
      rating: { average: 4.7, count: 1800 },
      enrollmentCount: 6750,
      thumbnail: "https://via.placeholder.com/300x200/8B5CF6/FFFFFF?text=ML+Python",
      tags: ["Python", "AI", "Machine Learning"]
    },
    {
      id: 5,
      title: "Financial Accounting Basics",
      instructor: "CA Priya Sharma",
      institution: "Commerce College",
      description: "Understand the principles of financial accounting, balance sheets, income statements, and financial analysis.",
      category: "Economics",
      difficulty: "Beginner",
      duration: { weeks: 10, hoursPerWeek: 5 },
      rating: { average: 4.5, count: 950 },
      enrollmentCount: 9200,
      thumbnail: "https://via.placeholder.com/300x200/EF4444/FFFFFF?text=Accounting",
      tags: ["Finance", "Accounting", "Business"]
    },
    {
      id: 6,
      title: "Introduction to Psychology",
      instructor: "Dr. Anna Williams",
      institution: "Psychology Department",
      description: "Explore the human mind, behavior patterns, cognitive processes, and psychological theories.",
      category: "Social Sciences",
      difficulty: "Beginner",
      duration: { weeks: 12, hoursPerWeek: 4 },
      rating: { average: 4.8, count: 1650 },
      enrollmentCount: 11400,
      thumbnail: "https://via.placeholder.com/300x200/EC4899/FFFFFF?text=Psychology",
      tags: ["Psychology", "Behavior", "Mental Health"]
    }
  ];

  const categories = ['all', 'Computer Science', 'Management', 'Economics', 'Social Sciences'];
  const difficulties = ['all', 'Beginner', 'Intermediate', 'Advanced'];

  const filteredCourses = courses.filter(course => {
    const matchesSearch = course.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         course.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         course.instructor.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'all' || course.category === selectedCategory;
    const matchesDifficulty = selectedDifficulty === 'all' || course.difficulty === selectedDifficulty;
    
    return matchesSearch && matchesCategory && matchesDifficulty;
  });

  const renderStars = (rating) => {
    return (
      <div className="flex items-center space-x-1">
        {[...Array(5)].map((_, i) => (
          i < Math.floor(rating) ? (
            <StarSolid key={i} className="w-4 h-4 text-yellow-400" />
          ) : (
            <StarIcon key={i} className="w-4 h-4 text-gray-300" />
          )
        ))}
        <span className="ml-2 text-sm text-gray-600 dark:text-gray-400">
          {rating} ({courses.find(c => c.rating.average === rating)?.rating.count})
        </span>
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
            Explore Courses
          </h1>
          <p className="text-xl text-gray-600 dark:text-gray-400 max-w-3xl mx-auto">
            Discover thousands of courses from top universities and institutions. 
            Start your learning journey today.
          </p>
        </div>

        {/* Search and Filters */}
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {/* Search */}
            <div className="relative">
              <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                placeholder="Search courses..."
                className="form-input pl-10"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            
            {/* Category Filter */}
            <select
              className="form-input"
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
            >
              {categories.map(category => (
                <option key={category} value={category}>
                  {category === 'all' ? 'All Categories' : category}
                </option>
              ))}
            </select>
            
            {/* Difficulty Filter */}
            <select
              className="form-input"
              value={selectedDifficulty}
              onChange={(e) => setSelectedDifficulty(e.target.value)}
            >
              {difficulties.map(difficulty => (
                <option key={difficulty} value={difficulty}>
                  {difficulty === 'all' ? 'All Levels' : difficulty}
                </option>
              ))}
            </select>
          </div>
        </div>

        {/* Results Count */}
        <div className="mb-6">
          <p className="text-gray-600 dark:text-gray-400">
            Showing {filteredCourses.length} of {courses.length} courses
          </p>
        </div>

        {/* Course Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredCourses.map(course => (
            <div key={course.id} className="bg-white dark:bg-gray-800 rounded-lg shadow-md hover:shadow-lg transition-shadow duration-200">
              {/* Course Image */}
              <img
                src={course.thumbnail}
                alt={course.title}
                className="w-full h-48 object-cover rounded-t-lg"
              />
              
              {/* Course Content */}
              <div className="p-6">
                {/* Category Badge */}
                <span className="inline-block px-2 py-1 text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-800 dark:text-blue-100 rounded-full mb-2">
                  {course.category}
                </span>
                
                {/* Title */}
                <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2 line-clamp-2">
                  {course.title}
                </h3>
                
                {/* Instructor */}
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  by {course.instructor}  {course.institution}
                </p>
                
                {/* Description */}
                <p className="text-gray-600 dark:text-gray-400 text-sm mb-4 line-clamp-3">
                  {course.description}
                </p>
                
                {/* Rating */}
                <div className="mb-4">
                  {renderStars(course.rating.average)}
                </div>
                
                {/* Course Details */}
                <div className="flex items-center justify-between text-sm text-gray-500 dark:text-gray-400 mb-4">
                  <div className="flex items-center space-x-1">
                    <ClockIcon className="w-4 h-4" />
                    <span>{course.duration.weeks} weeks</span>
                  </div>
                  <div className="flex items-center space-x-1">
                    <UserGroupIcon className="w-4 h-4" />
                    <span>{course.enrollmentCount.toLocaleString()}</span>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs ${
                    course.difficulty === 'Beginner' ? 'bg-green-100 text-green-800 dark:bg-green-800 dark:text-green-100' :
                    course.difficulty === 'Intermediate' ? 'bg-yellow-100 text-yellow-800 dark:bg-yellow-800 dark:text-yellow-100' :
                    'bg-red-100 text-red-800 dark:bg-red-800 dark:text-red-100'
                  }`}>
                    {course.difficulty}
                  </span>
                </div>
                
                {/* Tags */}
                <div className="flex flex-wrap gap-2 mb-4">
                  {course.tags.slice(0, 3).map((tag, index) => (
                    <span key={index} className="px-2 py-1 bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded-md text-xs">
                      {tag}
                    </span>
                  ))}
                </div>
                
                {/* Action Button */}
                <Link
                  to={`/courses/${course.id}`}
                  className="w-full btn-primary text-center block"
                >
                  View Course
                </Link>
              </div>
            </div>
          ))}
        </div>

        {/* Empty State */}
        {filteredCourses.length === 0 && (
          <div className="text-center py-16">
            <AcademicCapIcon className="mx-auto w-16 h-16 text-gray-400 mb-4" />
            <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
              No courses found
            </h3>
            <p className="text-gray-600 dark:text-gray-400">
              Try adjusting your search criteria or browse all courses.
            </p>
            <button
              onClick={() => {
                setSearchTerm('');
                setSelectedCategory('all');
                setSelectedDifficulty('all');
              }}
              className="mt-4 btn-primary"
            >
              Clear Filters
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default CoursesPage;
